/* purgecss start ignore */

.lc-icon {
  width: 24px;
  height: 24px;
}

.lc-icon,
  .lc-icon-decorative {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    vertical-align: middle;
  }

.lc-icon svg {
    width: 100%;
    height: 100%;
    color: inherit;
  }

.lc-icon:not(.lc-icon-color) svg,
  .lc-icon:not(.lc-icon-color) path {
    fill: currentColor;
  }

.lc-icon-decorative {
    width: 32px;
    height: 32px;
    border-radius: 4px;
  }

.lc-icon-decorative .lc-icon {
      width: 20px;
      height: 20px;
    }

.lc-icon-decorative:not(.lc-icon-color) svg,
    .lc-icon-decorative:not(.lc-icon-color) path {
      fill: white;
    }

/* purgecss end ignore */
